body {
  background-color: rgb(55, 65, 71);
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}


#popup-container {
  color: white;
}

#close-popup {
  position: absolute;
  top: 5%;
  left: 81%;
  z-index: 1;
}

#img-popup {
  position: absolute;
  top: 5%;
  left: 40%;
  height: 700px;
  opacity: 0.97;
  
}

header {
  padding-top: 10px;
  text-align: center;
  margin: -22px auto;
  background-color: rgb(39, 39, 39);
  color: aliceblue;
}

header > h1 {
  padding-bottom: 10px;
}

#header-book {
  margin: -28px -10px;
  text-align: center;
  margin-bottom: 1px;
  background-color: rgb(39, 39, 39);
  color: aliceblue;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

aside {
  float: left;
  margin-right: 15px;
}

#book-list-container {
  display: flex;
  flex-wrap: wrap;
  margin: 0 260px;
}

#book-list-container > section {
  background-color: #918d8d;
  color: aliceblue;
  width: 300px;
  padding: 10px;
  height: 210px;
  margin: 10px;
  font-size: 15px;
}

img {
  border: rgb(7, 34, 58) 2px solid;
}

#title-messenger {
  margin-top: 13px;
  color: azure;
}

#green {
  margin: 15px 5px;
  display: inline-grid;
  width: 15px;
  height: 15px;
  border-radius: 50%;
  background-color: rgb(24, 185, 24);
  border: rgb(0, 0, 0) 1px solid;
  box-shadow: inset -2px -3px 5px black;
}

#red {
  margin: 15px 5px;
  display: inline-grid;
  width: 15px;
  height: 15px;
  border-radius: 50%;
  background-color: rgb(255, 5, 5);
  border: rgb(0, 0, 0) 1px solid;
  box-shadow: inset -2px -3px 5px black;
}

#button-messenger {
  margin: 5px;
  background-color: rgb(63, 59, 59);
  color: rgb(198, 202, 206);
}

#footer {
  position: fixed;
  left: 0;
  bottom: 0;
  width: 100%;
  background-color: rgb(39, 39, 39);
  color: aliceblue;
  text-align: center;
}
